@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300;1,400;1,600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fira+Code&display=swap");
*:not(.CodeMirror),
button {
  font-family: Nunito !important;
}

body {
  overflow: hidden;
}

textarea {
  font-family: fira code !important;
  font-size: 25px !important;
}

body {
  margin: 0;
}

.top-pane {
  background-color: hsl(255, 6%, 25%);
}

.pane {
  height: 50vh;
  display: flex;
}

.pane.bottom-pane {
  padding: 0.7rem;
  padding-top: 0rem;
  padding-bottom: 0.1rem;
  background-color: hsl(255, 6%, 25%);
}

.pane.bottom-pane iframe {
  border-radius: 7px;
  height: 320px;
}

.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: hsl(255, 6%, 25%);
  transition: 0.3s all linear;
}

.editor-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);
  color: white;
  padding: 0.5rem;
  padding-left: 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}

.CodeMirror {
  height: 100% !important;
  font-family: fira code !important;
  font-size: 18px !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  overflow: hidden !important;
}

div.CodeMirror-scroll {
  overflow: hidden !important;
}

.editor-container.collapsed {
  flex-grow: 0;
  transition: 0.3s all linear;
}

.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
}

.expand-collapse-button {
  margin-left: 0.5rem;
  justify-content: space-between;
  background-color: none;
  background: none;
  color: white;
  cursor: pointer;
  border: none;
  outline: none;
}
